    }

    .aboutUs .mission-item{
      flex-direction: column;
    }

    .aboutUs .mission-item:nth-of-type(2) {
      flex-direction: column-reverse;
    }

    .article-content p {
      padding: 20px 20px;
    }


 
  }

  @media screen and (max-width: 550px) {
