      right: -100%;
      width: 100%;
      height: 100%;
  }

  .app-bar-navigation .user-login a {
      font-size: 15px;
  }

    .aboutUs .jumbotron p {
      padding: 40px 20px;
    }

    .aboutUs .vission{
      padding: 40px 20px;
    }

    .aboutUs .mission{
      padding: 40px 20px;
    }

    .aboutUs .mission-item{
      margin: 0;
      gap: 10px;
      padding-top: 25px;

    }

    .article-page .featured-image {
      width: 90%;
    }

    .province-main-content{
      display: grid;
      padding: 50px 20px 50px 20px;
  }

  .overlay-text {
    left: 45%;
  }
  footer-bar {
    flex-direction: column;
    align-items: center;
  }
  footer-bar .bottom{
    padding-right: 0;
  }
  footer-bar .upper{
    padding-left:0;
  }

  }
  
  @media screen and (max-width: 440px) {
    #hamburger{
      font-size: 30px;
    }

    .app-bar-navigation ul li a {
      font-size: 1.5rem;
  }
    .kategori{
      grid-template-columns: repeat(1, 1fr);
    }

    .search-section {
      justify-content: center;
    }

    .kategori-items{
      max-width: 250px;
      max-height: 250px;
    }

  .article-content h1 {
    margin-top: 0;
    font-size: 1.5rem;
  }

  .hero-element h1{
    font-size: 5rem;
    padding-left: 20px;
    padding-right: 20px;
  }

  .overlay-text{
    width: 90%;
    padding-left: 10px;
    h1{
      font-size: 28px;
    }
    p{
      font-size: 14px;
    }
   }

  .search-bar input{
    font-size: 1rem;
    max-width: 100%;
  }

  .article-item h3{
    font-size: 1.3rem;
}

  .article-item h4{
    font-size: 1rem;
  }

  .article-item p{
    font-size: 0.8rem;
}
footer-bar img {
  max-width: 90%;
}

}

@media screen and (max-width: 390px) {
  .appbar-logo{
    width: 100px;
  }

  .hero-content{
    h1{
      font-size: 4rem;
    }
    p{
      font-size: 13px;
    }
  }

  .search-bar input{
    max-width: 95%;
  }

  .recent-item{
    width: 95%;
  }
 .overlay-text{
  h1{
    font-size: 20px;
  }
  p{
    font-size: 10px;
  }
 }

 .tentang{
  h2{
    font-size: 20px;
  }
  p{
    font-size: 13px;
  }
 }

 .history{
  h2{
    font-size: 20px;
  }
  .province-search-bar{
    width: 100%;
    justify-content: center;

    input{
      width: 70%;
    }

    button{
      width: 30%;
      font-size: 12px;
    }
  }
  .article-container{
    width: 100%;
  }
 }
 .culture-card{
  width: 90%;
 }

