/* 
Province
 */
.province-detail h2{
    font-size: 2.4rem;
}

.province-detail p{
    font-size: 1.4rem;
}

.header-content{
    height: 100vh;
    position: relative;
}

.header-content img{
    width: 100%;
    height: 100vh;
    object-fit: cover;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); 
    filter: brightness(0.7);
    z-index: -1;
}

.overlay-text {
    position: absolute;
    top: 60%;
    left: 35%;
    transform: translate(-50%, -50%);
    text-align: center;
    color: white;
}

.overlay-text h1{
    font-size: 4rem;
    text-align: left;
}
.overlay-text p {
    font-size: 1.3rem;
    text-align: left;
    padding-top: 20px;
}

.province-main-content{
    padding: 50px 60px 20px 60px;
}

.tentang p{
    padding-top: 10px;
    padding-left: 10px;
    gap: 20px;
}

.culture{
    padding-top: 50px;
    padding-bottom: 50px;
}

.culture h2, .history h2{
    padding-bottom: 30px;
}

.culture-card-list{
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 20px;
    
    align-items: center;
    justify-items: center;
}

.culture-card {
    position: relative;
    width: 300px;
    height: 250px;
    overflow: hidden;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); 
  }
  
.culture-card img {
    width: 100%; 
    height: 100%;
    object-fit: cover;
    object-position: center; 
}
  
  .culture-info {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    background: rgba(0, 0, 0, 0.5);
    color: white;
    padding: 5px;
    box-sizing: border-box;
  }
  
  .culture-info h3,
  .culture-info p {
    margin: 5px 0;
  }

  .history{
    padding: 0px 30px 20px 30px;
}

  .province-search-bar {
    display: flex;
    justify-content: flex-end;
    margin-bottom: 20px;
  }
  
  .province-search-bar input {
    background-color: #f4f4f4;
    padding: 8px;
    border-radius: 5px;
    font-size: 16px;
    margin-right: 10px;
  }
  
  .province-search-bar button {
    padding: 8px 15px;
    background-color: #543310;
    color: white;
    border: none;
    cursor: pointer;
  }

.article{
    position: relative;
    overflow: hidden;
}

.article-list{
    display: grid;
    gap: 20px;

}

.article-item {
    text-decoration: none;
    color: black;
    gap: 20px;
    background-color: #FFFDF5;
    border: 2px solid black;
    padding: 20px 30px 20px 30px;
    border-radius: 5px;
    position: relative;
  }

.article-item h3{
    font-size: 1.8rem;
    font-weight: 700;
    padding-bottom: 5px;
}

.article-item p{
    font-size: 1.3rem;
    padding-bottom: 20px;
}

.article-item a{
    text-decoration: none;
    color: black;
}

.article-item h4{
    position: relative;
    font-size: 1.2rem;
    font-weight: 500;
    overflow: hidden;
    text-overflow: ellipsis;
    max-width: 100%;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 3;
    white-space: normal;
}

.notFoundArticle{
    font: 1rem;
    text-align: center;
}`, "",{"version":3,"sources":["webpack://./src/styles/provinceDetail.css"],"names":[],"mappings":";AACA;;EAEE;AACF;IACI,iBAAiB;AACrB;;AAEA;IACI,iBAAiB;AACrB;;AAEA;IACI,aAAa;IACb,kBAAkB;AACtB;;AAEA;IACI,WAAW;IACX,aAAa;IACb,iBAAiB;IACjB,wCAAwC;IACxC,uBAAuB;IACvB,WAAW;AACf;;AAEA;IACI,kBAAkB;IAClB,QAAQ;IACR,SAAS;IACT,gCAAgC;IAChC,kBAAkB;IAClB,YAAY;AAChB;;AAEA;IACI,eAAe;IACf,gBAAgB;AACpB;AACA;IACI,iBAAiB;IACjB,gBAAgB;IAChB,iBAAiB;AACrB;;AAEA;IACI,4BAA4B;AAChC;;AAEA;IACI,iBAAiB;IACjB,kBAAkB;IAClB,SAAS;AACb;;AAEA;IACI,iBAAiB;IACjB,oBAAoB;AACxB;;AAEA;IACI,oBAAoB;AACxB;;AAEA;IACI,aAAa;IACb,qCAAqC;IACrC,SAAS;;IAET,mBAAmB;IACnB,qBAAqB;AACzB;;AAEA;IACI,kBAAkB;IAClB,YAAY;IACZ,aAAa;IACb,gBAAgB;IAChB,mBAAmB;IACnB,wCAAwC;EAC1C;;AAEF;IACI,WAAW;IACX,YAAY;IACZ,iBAAiB;IACjB,uBAAuB;AAC3B;;EAEE;IACE,kBAAkB;IAClB,SAAS;IACT,OAAO;IACP,WAAW;IACX,8BAA8B;IAC9B,YAAY;IACZ,YAAY;IACZ,sBAAsB;EACxB;;EAEA;;IAEE,aAAa;EACf;;EAEA;IACE,2BAA2B;AAC/B;;EAEE;IACE,aAAa;IACb,yBAAyB;IACzB,mBAAmB;EACrB;;EAEA;IACE,yBAAyB;IACzB,YAAY;IACZ,kBAAkB;IAClB,eAAe;IACf,kBAAkB;EACpB;;EAEA;IACE,iBAAiB;IACjB,yBAAyB;IACzB,YAAY;IACZ,YAAY;IACZ,eAAe;EACjB;;AAEF;IACI,kBAAkB;IAClB,gBAAgB;AACpB;;AAEA;IACI,aAAa;IACb,SAAS;;AAEb;;AAEA;IACI,qBAAqB;IACrB,YAAY;IACZ,SAAS;IACT,yBAAyB;IACzB,uBAAuB;IACvB,4BAA4B;IAC5B,kBAAkB;IAClB,kBAAkB;EACpB;;AAEF;IACI,iBAAiB;IACjB,gBAAgB;IAChB,mBAAmB;AACvB;;AAEA;IACI,iBAAiB;IACjB,oBAAoB;AACxB;;AAEA;IACI,qBAAqB;IACrB,YAAY;AAChB;;AAEA;IACI,kBAAkB;IAClB,iBAAiB;IACjB,gBAAgB;IAChB,gBAAgB;IAChB,uBAAuB;IACvB,eAAe;IACf,oBAAoB;IACpB,4BAA4B;IAC5B,qBAAqB;IACrB,mBAAmB;AACvB;;AAEA;IACI,UAAU;IACV,kBAAkB;AACtB","sourcesContent":["\r\n/* \r\nProvince\r\n */\r\n.province-detail h2{\r\n    font-size: 2.4rem;\r\n}\r\n\r\n.province-detail p{\r\n    font-size: 1.4rem;\r\n}\r\n\r\n.header-content{\r\n    height: 100vh;\r\n    position: relative;\r\n}\r\n\r\n.header-content img{\r\n    width: 100%;\r\n    height: 100vh;\r\n    object-fit: cover;\r\n    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); \r\n    filter: brightness(0.7);\r\